:root {
  --color-darkBlue: #073d5d;
  --color-darkBlue-lignt: #0a476b;
  --color-main: #42a5f5;
  --color-bg-dark: #f5f5f5;
  --color-bg: #fdfffd;
  --color-font: #073d5d;
  --color-font-reverse: white;
  --color-accent: #073d5d;
  --color-font-accent: white;
  --color-error-bg: rgba(173, 173, 173, 0.401);
  --color-popup-bg: white;
  --color-shadow: rgba(67, 67, 67, 0.401);
  --color-mainFontColor: #42a5f5;
  --color-tondownMainFontColor: #6fbbf7;
  --color-buttonFontColor: white;
  --color-bodyBackgroundColor: #073d5d;
  --color-unActivedColor: rgb(177, 177, 177);
  --color-overBackgroundColor: rgb(128, 128, 128, 0.5);
  /* 사이즈 */
  --font-size-very-large: 1.6rem;
  --font-size-medium: 1.4rem;
  --font-size-small-medium: 1.2rem;
  --font-size-small: 1rem;
  --font-header-icon: 1.8rem;
  --font-mobile-small: 0.8rem;
  --font-mobile-medium: 1.2rem;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--color-darkBlue);
  display: flex;
  flex-direction: row;
  justify-content: center;
}
body,
html {
  width: 100%;
  height: 100%;
}
* {
  box-sizing: border-box;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  height: 100%;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

.app {
  height: 100vh;
  width: 100vw;

  max-width: 600px;
  background-color: white;
  display: flex;

  flex-direction: column;
}

.transition-group {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.fade-enter {
  opacity: 0;
  transform: scale(1.2);
}

.fade-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: all 0.5s ease;
}

.fade-exit {
  opacity: 1;
  transform: scale(1);
}

.fade-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: all 0.5s ease;
}
